/* Styles for the input range */
input[type=range] {
  -webkit-appearance: none;
  width: 100%;
  height: 10px;
  background-image: linear-gradient(to right, orange 0%, orange 50%, black 50%, black 100%);
  border-radius: 5px;
  outline: none;
  cursor: pointer;
}

/* Styles for the thumb in the 0-12 range */
input.range-0-12::-webkit-slider-thumb {
  -webkit-appearance: none;
  width: 20px;
  height: 20px;
  background-image: radial-gradient(circle, yellow 40%, gold 45%);
  border-radius: 50%;
}

/* Styles for the thumb in the 12-23 range */
input.range-12-23::-webkit-slider-thumb {
  -webkit-appearance: none;
  width: 20px;
  height: 20px;
  background-image: radial-gradient(circle, yellow 40%, gold 45%);
  border-radius: 50%;
}

/* Box-shadow for thumb in the 0-12 range */
input.range-0-12::-webkit-slider-thumb:before {
  content: "";
  position: absolute;
  width: 100%;
  height: 100%;
  background-image: radial-gradient(circle, orange 40%, rgb(0, 0, 0) 45%);
  border-radius: 50%;
  box-shadow: 0 0 0 5px transparent; /* Set the initial box-shadow */
}

/* Box-shadow for thumb in the 12-23 range */
input.range-12-23::-webkit-slider-thumb:before {
  content: "";
  position: absolute;
  width: 100%;
  height: 100%;
  background-image: radial-gradient(circle, orange 40%, black 45%);
  border-radius: 50%;
  box-shadow: 0 0 0 5px transparent; /* Set the initial box-shadow */
}

/* Remove outline on focus */
input[type=range]:focus {
  outline: none;
}





